.ez-c {
  width: 30%;
  float: left;
  margin-right: 2rem; }

section {
  padding: 1rem 0; }

* {
  margin: 0;
  padding: 0; }

.slider {
  width: 30vw;
  text-align: left;
  margin: 14px auto; }

/*demo global*/
:root {
  font-family: 'Open Sans', 'Helvetica Neue', 'Arial', sans-serif;
  font-size: 14px;
  background-color: #FFF;
  --blue: #65a0a9;
  --darkblue: #125084;
  --gray: #DFE6EB;
  --darkgray: #6F7981;
  --almostwhite: #F9F9F9;
  --dark: #22292E;
  --radius: .5rem; }

input[type="range"] {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  vertical-align: middle;
  margin-bottom: .75rem;
  border-radius: 10px;
  height: 1.1em; }

::-moz-range-track {
  background: transparent;
  border: 0; }

input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  cursor: pointer;
  border: 0;
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s; }

input[type="range"]::-moz-range-thumb {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  cursor: pointer;
  border: 0;
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s; }

input[type="range"]::-ms-thumb {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  cursor: pointer;
  border: 0;
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s; }

.range-value {
  min-width: 2em;
  text-align: center;
  background-color: var(--almostwhite);
  color: var(--dark);
  padding: .5rem;
  display: inline-block;
  border-radius: var(--radius); }

.range-label {
  display: block;
  text-transform: uppercase;
  color: var(--almostwhite);
  margin-bottom: 1rem; }
  .range-label .count {
    color: #b5dfe3;
    font-style: italic;
    text-decoration: underline; }

.attached-range {
  margin-bottom: 0px !important;
  z-index: 5;
  position: relative; }

.attached-range + .range-value {
  z-index: 1;
  position: relative;
  margin-top: -5px;
  margin-left: 10px;
  padding: .35rem .75rem;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  -webkit-box-shadow: inset 0 2px 4px #ddd;
          box-shadow: inset 0 2px 4px #ddd; }

.attached-range + .range-value:after {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  position: absolute;
  z-index: 0;
  content: '';
  top: -5px;
  right: -4px;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-right: 6px solid #eee;
  border-bottom: 6px solid transparent; }

.attached-range + .range-value:before {
  -webkit-transform: rotate(130deg);
      -ms-transform: rotate(130deg);
          transform: rotate(130deg);
  position: absolute;
  z-index: 0;
  content: '';
  top: -5px;
  left: -4px;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-right: 6px solid #eee;
  border-bottom: 6px solid transparent; }

/* range sizes */
.normal-range {
  width: 100%;
  height: .5rem; }

.normal-range::-webkit-slider-thumb {
  width: 1rem;
  height: 1rem;
  border-radius: 50%; }

.normal-range::-moz-range-thumb {
  width: 1rem;
  height: 1rem;
  border-radius: 50%; }

.normal-range::-ms-thumb {
  width: 1rem;
  height: 1rem;
  border-radius: 50%; }

/* range styles */
.primary-range {
  background: var(--gray); }

.primary-range::-webkit-slider-thumb {
  background-color: var(--blue); }

.primary-range::-webkit-slider-thumb:hover {
  background-color: var(--darkblue); }

.primary-range::-moz-range-thumb {
  background-color: var(--blue); }

.primary-range::-moz-range-thumb:hover {
  background-color: var(--darkblue); }

.primary-range::-ms-thumb {
  background-color: var(--blue); }

.primary-range::-ms-thumb:hover {
  background-color: var(--darkblue); }



